@import url(https://fonts.googleapis.com/css2?family=Lato:wght@900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Article figure{margin-left:0}.Article a{font:700 12px "Roboto", sans-serif;color:#f07e26;text-decoration:none}.Article__header h2{font-family:"Lato", sans-serif;font-size:2.25rem;line-height:1.1;font-weight:lighter}.Article__header .list_title{font:bold 0.7rem "Roboto", sans-serif}.Article__image-rounded img{width:70px;height:70px;height:auto}

.Article_list .Article{display:grid;grid-template-columns:8rem 1fr;border:1px 1px 0 1px solid lime}.Article_list .Article__header a{font:700 2px "Roboto", sans-serif;color:#f07e26;text-decoration:none}

.App{position:relative}.App__header{position:fixed;height:100vh;overflow:hidden;width:10rem;border-right:1px solid #e0e0e0;border-top:10px solid #f07e26}.App__header-navigation{padding:1.5rem 0 0 2rem}.App__subscribe{position:absolute;bottom:10px;width:100%;border-top:1px dotted #00a1ab;font:bold 0.7rem "Roboto", sans-serif}.App__subscribe h2{background:#00a1ab;margin:1rem;text-align:center;padding:2px;border-radius:2px;color:ghostwhite}.App__main-wrapper{margin-left:12rem;position:relative}.App__main-wrapper .carousel{height:5rem}.App__main-wrapper .frontpage-list{width:55vw}.App__main-wrapper .latest-list{position:absolute;display:grid;width:20rem;top:5rem;right:2rem;height:calc(100vh - 6rem);overflow:scroll;border:solid 1px #e0e0e0}.App__main-wrapper .latest-list::-webkit-scrollbar{display:none}.App__main-wrapper .latest-list .header{border-bottom:solid 1px #e0e0e0;border-top:5px solid #f07e26;display:flex;align-items:center;padding-left:10px}.App__main-wrapper .latest-list .header h1{font:bold 0.7rem "Roboto", sans-serif;margin-left:10px}.App__main-wrapper .sticky{position:fixed;top:0}

